.skills-section {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100vw;
    flex-direction: column;
}

.hover-effect{  
    transition: all ease-in-out;
    transition-duration: 1s;
    transition-property: transform, letter-spacing;
    text-align: center;
}

.hover-effect:hover {
    transform: scale(1.2);
    letter-spacing: 10px;
}

.skill {
    width: 100vw;
}

.skill-list {
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: row;
}

#card {
    max-width: 18rem;
}

.swiper-slide {
    display: flex;
    align-items: center;
    justify-content: center;
}

.mySwiper {
    position: relative;
    width: 70vw;
}

.card-img-top {
    padding: 25px;
}

@media (max-width: 600px) {
    .mySwiper {
        padding: 0;
        width: 100vw;
    }

    .card-img-top {
        padding: 10px;
    }
}