.nav-item {
    margin-left: 20px;
}

.Hover {
    transition: transform 0.5s ease-in-out;
}

.Hover:hover {
    transform: scale(1.2);
}

@media (max-width: 992px) {

    .nav-item {
        margin-left: 0;
    }

    .Hover {
        transition: none;
    }

    .Hover:hover {
        transform: none;
    }
}