/* @import url('https://fonts.googleapis.com/css2?family=Cedarville+Cursive&display=swap'); */

.divbox {
  display: flex;
  align-items: center;
  justify-content: space-around;
  position: relative;
  padding: 20px;
}

#box1,
#box2 {
  padding: 0;
  margin: 0;
  width: 50vw;
  display: flex;
  align-items: center;
  justify-content: center;
}


/* Designing of Avtar on website */

#avtar {
  position: relative;
  border-radius: 50%;
  width: 400px;
  height: 400px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.avtar-light {
  box-shadow: inset 2px 2px 30px rgb(200, 200, 200);
}

.avtar-light::after {
  box-shadow: inset 2px 2px 30px rgb(200, 200, 200);
  background-color: #d8f9ff;
}

.avtar-dark {
  box-shadow: inset 2px 2px 30px rgb(60, 60, 60);
}

.avtar-dark::after {
  box-shadow: inset 2px 2px 30px rgb(60, 60, 60);
  background-color: rgb(40, 40, 40);
}

#avtar::before {
  content: "";
  position: absolute;
  width: 350px;
  height: 350px;
  z-index: 0;
  overflow: hidden;
  animation: anim 5s linear infinite;
  border-radius: 50%;
  background: linear-gradient(to top, #ff0000, #ff7f00, #ffff00, #00ff00, #0000ff, #4b0082, #9400d3);
}

#avtar::after {
  background-image: url(./img/logo.png);
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  border: 2px solid transparent;
  border-radius: 50%;
  content: "";
  position: absolute;
  width: 340px;
  height: 340px;
  z-index: 1;
}

@keyframes anim {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* Designing of Text Animation on website */

#heading {
  margin-left: 30px;
  position: relative;
  width: 100%;
  height: 200px;
  display: inline-block;
  align-items: center;
}

.heading {
  position: relative;  
  font-family: 'Nerko One';
  font-size: 30px;
  font-weight: 400;
  overflow: hidden;
  width: 220px;
}

#text_change {
  position: relative;
  width: 100%;
  word-spacing: 10px;
  font-family: 'Nerko One', cursive;
  font-size: 30px;
}

#text_change::after {
  content: 'A';
  color:transparent;
  position: absolute;
  width: 120%;
  left: 0;
  animation: typing 3s steps(12) infinite;
}

.text_change_light::after {
  background-color: #d8f9ff;
  border-left: 2px solid black;
}

.text_change_dark::after {
  background-color: rgb(40, 40, 40);
  border-left: 2px solid white;
}

@keyframes typing {
  0%, 85%, 100% {
    left: 0;
  }

  35%, 55% {
    left: calc(100% + 2px);
  }
}


/* Block of code to create a responsive website */

@media (max-width: 992px) {
  .divbox {
    flex-direction: column-reverse;
  }

  #box1,
  #box2 {
    width: 100vw;
  }

  #avtar {
    width: 320px;
    height: 320px;
  }

  #avtar::before {
    width: 280px;
    height: 280px;
  }

  #avtar::after {
    width: 270px;
    height: 270px;
  }

  .heading {
    font-family: 'Times New Roman', Times, serif;
  }
}


/* About website */

.Box {
  margin-top:-40px ;
  padding: 20px;
}

#webInfo {
  font-family: 'Times New Roman', Times, serif;
  border-left: 3px solid;
  border-right: 3px solid;
  border-radius: 50px ;
  font-size: 1.2rem;
  padding: 20px;
  animation: blink 2s linear infinite;
}

@keyframes blink {
  0%, 100% {
    border-color: #9400d3;
  }

  14.285714286% {
    border-color: #4b0082;
  }

  28.571428571% {
    border-color: #0000ff;
  }

  42.857142857% {
    border-color: #00ff00;
  }
  
  57.142857143% {
    border-color: #ffff00;
  }

  71.428571429% {
    border-color: #ff7f00;
  }

  85.714285714% {
    border-color: #ff0000;
  }
}

